import React, { useEffect } from "react";
import { Route, Redirect, useRouteMatch } from "react-router-dom";
// Sections
import Blog from "./Blog";
import About from "./About";
import Intro from "./Intro";
import Header from "./Header";
import Resume from "./Resume";
import Service from "./Service";
import Contact from "./Contact";
import Portfolio from "./Portfolio";
import Testimonial from "./Testimonial";
// Blog Posts
import FirstBlogPost from "./BlogPosts/FirstBlogPost";
import SecondBlogPost from "./BlogPosts/SecondBlogPost";
import ThirdBlogPost from "./BlogPosts/ThirdBlogPost";
import FourthBlogPost from "./BlogPosts/FourthBlogPost";
import FifthBlogPost from "./BlogPosts/FifthBlogPost";
import SixthBlogPost from "./BlogPosts/SixthBlogPost";
import SeventhBlogPost from "./BlogPosts/SeventhBlogPost";
import EighthBlogPost from "./BlogPosts/EighthBlogPost";
// Components
import Helmet from "./common/Helmet";
import Switch from "./common/Switch";
import RedirectAs404 from "./common/RedirectAs404";

const routes = [
    {
        path: "/intro",
        component: <Intro />,
    },
    {
        path: "/about",
        component: <About />,
    },
    {
        path: "/resume",
        component: <Resume />,
    },
    // {
    //     path: "/service",
    //     component: <Service />,
    // },
    // {
    //     path: "/portfolio",
    //     component: <Portfolio />,
    // },
    {
        path: "/blog",
        component: <Blog />,
    },
    // {
    //     path: "/blog/BlogPosts/single-post",
    //     component: <BlogSinglePost />,
    // },
    {
        path: "/blog/BlogPosts/first-post",
        component: <FirstBlogPost />,
    },
    {
        path: "/blog/BlogPosts/second-post",
        component: <SecondBlogPost />,
    },
    {
        path: "/blog/BlogPosts/third-post",
        component: <ThirdBlogPost />,
    },
    {
        path: "/blog/BlogPosts/fourth-post",
        component: <FourthBlogPost />,
    },
    {
        path: "/blog/BlogPosts/fifth-post",
        component: <FifthBlogPost />,
    },
    {
        path: "/blog/BlogPosts/sixth-post",
        component: <SixthBlogPost />,
    },
    {
        path: "/blog/BlogPosts/seventh-post",
        component: <SeventhBlogPost />,
    },
    {
        path: "/blog/BlogPosts/eighth-post",
        component: <EighthBlogPost />,
    },
    // {
    //     path: "/testimonial",
    //     component: <Testimonial />,
    // },
    // {
    //     path: "/contact",
    //     component: <Contact />,
    // },
];

function Home() {
    let { path } = useRouteMatch();

    useEffect(() => {
        document.documentElement.className = "home-1 skin-13";
        return () => {
            document.documentElement.className = "";
        };
    });

    return (
        <div>
            <Helmet title="Adam Hunter" />
            <Header />
            <Switch>
                <Route path={path} exact>
                    <Redirect
                        to={{
                            pathname: `${path}/intro`.replace(/([^:])(\/\/+)/g, "$1/"),
                        }}
                    />
                </Route>
                {routes.map((item, index) => (
                    <Route key={index} path={`${path}${item.path}`} exact>
                        {item.component}
                    </Route>
                ))}
                <Route component={RedirectAs404} />
            </Switch>
        </div>
    );
}

export default Home;
